// const w = document.body.clientWidth
// const h = document.body.clientHeight
const directions = {
  southEast: { offset: 0, x: 2, y: 1, opposite: 'northWest' },
  northWest: { offset: 20, x: -2, y: -1, opposite: 'southEast' },
  southWest: { offset: 224, x: -2, y: 1, opposite: 'northEast' },
  northEast: { offset: 96, x: 2, y: -1, opposite: 'southWest' }
}
const anims = {
  drive: { startFrame: 0, endFrame: 19, speed: 0.02 }
}

export default class HumanMainScene extends Scene {
  constructor () {
    super({ key: 'HumanMainScene' })
  }
  init () {
    console.log('HumanMainScene init')
    gameFunction = new GameFunction(this)

    scene = this
    scene.buildingGroup = this.add.group()

    // 設定游標
    pointer = scene.input.activePointer

    // 設定背景
    scene.add.image(w/2, h/2, 'bg').setDisplaySize(2*w, 2*h).setScrollFactor(0)
    
    // 畫面點擊事件
    scene.input.on('pointerdown', function() {
      import('../../store/modules/planet').then(module => {
        module.default.mutations.setBuildingElement(null, false)
      })
      gameFunction.cancelJump()
      // scene.buildingGroup.getChildren().map(ele => {ele.input.enabled = true})
    })

    text = scene.add.image(w/2, h/2 ,'kyronusDisplayText').setScrollFactor(0)
    text.depth = 10000
    text.alpha = 0
  }
  preload () {
    // let url
    // url = 'https://raw.githubusercontent.com/rexrainbow/phaser3-rex-notes/master/plugins/dist/rexpinchplugin.min.js'
    // this.load.plugin('rexpinchplugin', url, true)
  }
  create () {
    console.log('HumanMainScene create')
    // gameFunction = new GameFunction(this)
    
    scene.events.on('transitioncomplete', function(fromScene, duration){
      Obj.nowScene = 'HumanMainScene'
      console.log(Obj)
      let mapScale = userData.scale
      scene.cameras.main.setSize(w, h).centerOn(w/2, h/2+(mapScale-3)*48)
      scene.cameras.main.setZoom(0.5)

      
      setTimeout(() => {
        gameFunction.zoomInOut(1, 1000, 'Quart.easeOut')
        // 呈現字
          scene.tweens.add({
            targets: text,
            alpha: 1,
            duration: 1000,
            ease: 'Linear',
            onComplete: function () {
              scene.tweens.add({
                targets: text,
                alpha: 0,
                duration: 1000,
                ease: 'Linear',
                delay: 800
              })
            }
          })
      }, 250)
    })

    // pinch
    // var dragScale = scene.plugins.get('rexpinchplugin').add(scene);
    // dragScale.on('drag1', function (dragScale) {
    //   var drag1Vector = dragScale.drag1Vector;
    //   scene.cameras.main.scrollX -= drag1Vector.x / scene.cameras.main.zoom;
    //   scene.cameras.main.scrollY -= drag1Vector.y / scene.cameras.main.zoom;
    // }).on('pinch', function (dragScale) {
    //   var scaleFactor = dragScale.scaleFactor
    //   scene.cameras.main.zoom *= scaleFactor
    //   if(scene.cameras.main.zoom <= 0.5){
    //     scene.cameras.main.zoom = 0.5
    //   } else if (scene.cameras.main.zoom >= 2){
    //     scene.cameras.main.zoom = 2
    //   }
    // }, scene)

    // moving sprite car
    let Car = new Phaser.Class({
      Extends: Phaser.GameObjects.Image,
      initialize:
      function Car (scene, x, y, motion, direction, distance){
        this.startX = x
        this.startY = y
        this.distance = distance
        this.motion = motion
        this.anim = anims[motion]
        this.direction = directions[direction]
        this.speed = 0.5
        this.f = this.anim.startFrame

        Phaser.GameObjects.Image.call(this, scene, x, y, 'car', this.direction.offset + this.f)
        this.depth = y + 192
        scene.time.delayedCall(this.anim.speed * 1000, this.changeFrame, [], this)
      },
      changeFrame: function () {
        this.f++
        let delay = this.anim.speed;
        if (this.f === this.anim.endFrame){
          switch (this.motion) {
            case 'drive':
              this.f = this.anim.startFrame
              this.frame = this.texture.get(this.direction.offset + this.f)
              scene.time.delayedCall(delay * 1000, this.changeFrame, [], this)
              break
            default:
              break
          }
        } else {
          this.frame = this.texture.get(this.direction.offset + this.f)
          scene.time.delayedCall(delay * 1000, this.changeFrame, [], this)
        }
      },
      resetAnimation: function () {
        this.f = this.anim.startFrame
        this.frame = this.texture.get(this.direction.offset + this.f)
        scene.time.delayedCall(this.anim.speed * 1000, this.changeFrame, [], this)
      },
      update: function () {
        if ( this.motion === 'drive'){
          this.x += this.direction.x * this.speed

          if(this.direction.y !== 0){
            this.y += this.direction.y * this.speed
            this.depth = this.y + 192
          }

          // drive far enough?
          if (Phaser.Math.Distance.Between(this.startX, this.startY, this.x, this.y) >= this.distance){
            this.direction = directions[this.direction.opposite]
            this.f - this.anim.startFrame
            this.frame = this.texture.get(this.direction.offset + this.f)
            this.startX = this.x
            this.startY = this.y
          }
        }
      }
    })

    car.push(this.add.existing(new Car(this, w/2-96, h/2-96/2-32, 'drive', 'southEast', 150).setOrigin(0.5, 1)))
    car.push(this.add.existing(new Car(this, w/2+1*96-16, h/2+3*96/2-32-8, 'drive', 'northWest', 300).setOrigin(0.5, 1)))

    gameFunction.buildMap('tilesKyronus' , userData.scale)
 
    placeBuildings()

    // set camera init position
    let mapScale = userData.scale
    scene.cameras.main.setSize(w, h).centerOn(w/2, h/2+(mapScale-3)*48)
    // gameFunction.initZoomIn()
    scene.cameras.main.setZoom(0.5)
    setTimeout(() => {
      gameFunction.zoomInOut(1, 1000, 'Quart.easeOut')
    }, 250)
  }

  update () {
    car.forEach(function (car) { car.update() })
    
    // drag camera
    // pointer = scene.input.activePointer
    if(pointer.justMoved){
      let x = scene.cameras.main.midPoint.x - pointer.x + pointer.prevPosition.x
      let y = scene.cameras.main.midPoint.y - pointer.y + pointer.prevPosition.y
      scene.cameras.main.centerOn(x, y)
    }
  }
}

// function getBuildingMap (scale, map) {
//   userData.scale = scale
//   userData.buildingMap = map
// }

function placeBuildings () {
  userData.buildingMap.map(ele => {
    if (ele.status === 'ing') {
      if (ele.finishTime <= Date.now()){
        console.log('ing改completed')
        ele.status = 'completed'
        delete ele.finishTime
      }
    }

    switch (ele.status) {
      case 'done':
        gameFunction.createBuilding(ele.id, ele, false)
        break
      case 'completed':
        gameFunction.createBuilding('b1', ele, false)
        break
      case 'ing':
        gameFunction.createBuilding('b0', ele, false)
        break
    }
  })
}

export { gameFunction }